@import '../../Stylesheets/Variables.scss';

.main-panel {
    width: 70%;
    display: flex;
    flex-direction: column;
    margin-left: 30%;
    transition: 300ms ease-in-out;
    position: relative;
    z-index: 2;
    
    &__expanded {
        width: 70%;
        transition: 300ms ease-in-out;
        position: relative;
        z-index: 2;
        margin-left: 30%;
    }

    &__header {
        padding: 0.1rem;
        box-shadow: 0 4px 5px 0 $gray-lighter;
        font-family: 'Work Sans';
    }
}

.fa-caret-right {
    color: white;
}

.updated-date-component {
    box-shadow: 0 4px 5px 0 $gray-lightest;    
    &__date {
        padding-top: 0.5rem;
        font-family: 'Work Sans';
        margin: 0.2rem 0rem 0.2rem 1rem;
    }

    &__open-arrow {
        margin: 0.2rem 0rem 0.2rem 1rem;
    }
}