@import '../../../Stylesheets/Variables.scss';

.report-container {
    font-family: 'Work Sans';
    width: 50%;
    height: 56rem;
    overflow-x: auto;
    
    &__header {
        font-size: medium;
        padding-bottom: 1rem;
        padding: 4% 0% 0% 10%;
    }

    &__list {
        :hover {
            background-color: $brand-color-lightest;
            color: black;
        }
    }
}

.factor-list {
    font-size: medium;
    font-family: 'Work Sans';
    padding: 4% 0% 5% 10%;
    margin-left: 0rem;
    cursor: pointer;
    text-decoration: none;
    color: black;
}

.hashlink-component {
    text-decoration: none;
    color: black;
    font-size: medium;
    padding: 4% 0% 0% 10%;
}