@import '../../Stylesheets/Variables.scss';

.header-component {
    position: relative;
    height: 3rem;
    top: 0;
    left: 0;
    z-index: 99;
    box-shadow: 0 4px 5px 0 $gray-lighter;
    padding-top: 1rem;

    &__content {
        font-family: 'Work Sans', sans-serif;
        margin-left: 2rem;

        &__orange {
            color: $brand-color-alternate;
        }

        &__purple {
            color: $brand-color-dark;
        }
    }

}