@import '../../Stylesheets/Variables.scss';
body {
    height: 100%;
}

.main-panel {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-left: 0;
    transition: 300ms ease-in-out;
    position: relative;
    z-index: 2;
    
    &__expanded {
        width: 70%;
        transition: 300ms ease-in-out;
        position: relative;
        z-index: 2;
        margin-left: 30%;
    }

    &__content {
        text-align: center;
        font-size: large;
        width: 25rem;
        margin-top: 20%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: auto;
        font-family: 'Work Sans';
    }
}