@import '../../Stylesheets/Variables.scss';

.sidebar {
    width: 1%;
    background-size: 100%;
    background-position: 0% 100%;
    border-right: 0.1rem solid $brand-color-light;
    background-color: $brand-color-lightest;
    box-shadow: 0rem gray;
    position: absolute;
    top: 3rem;
    left: 0;
    height: 56rem;
    transform: translateX(-1px);
    transition: 10ms ease-in-out;
    z-index: 9;
    overflow: hidden;
    cursor: pointer;
    padding-top: 0rem;

    &__expanded {
        width: 30%;
        height: 56rem;
        position: absolute;
        background-size: 100%;
        background-position: 0 0;
        transform: translateX(0);
        background-color: white;
        transition: 10ms ease-in-out;
        padding-top: 0rem;
        border-right: 0.1rem solid $brand-color-light;
    }
}
